'use client'

import { useState } from 'react'
import Link from 'next/link'
import Navigation from '../../components/Navigation'
import { seriesOrder, artworks } from '../../content/artworks'

export default function PortfolioIndex() {
  const [hoveredSlug, setHoveredSlug] = useState<string | null>(null)

  return (
    <div style={{ backgroundColor: '#ffffff', minHeight: '100vh', color: '#000' }}>
      <div className="grain-overlay" style={{ opacity: 0.02 }} />
      <Navigation />

      <div style={{ 
        maxWidth: '1400px', 
        margin: '0 auto', 
        padding: '180px 60px 120px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center'
      }}>
        <ul style={{ 
          listStyle: 'none', 
          padding: 0, 
          margin: 0, 
          display: 'flex', 
          flexDirection: 'column', 
          alignItems: 'center',
          gap: '2vw'
        }}>
          {seriesOrder.map((series) => (
            <li key={series.slug} style={{ position: 'relative' }}>
              <Link 
                href={`/selected-works/${series.slug}`}
                onMouseEnter={() => setHoveredSlug(series.slug)}
                onMouseLeave={() => setHoveredSlug(null)}
                style={{ 
                  textDecoration: 'none', 
                  color: hoveredSlug && hoveredSlug !== series.slug ? '#eee' : '#000',
                  fontSize: 'clamp(40px, 8vw, 120px)',
                  fontWeight: 700,
                  letterSpacing: '-0.02em',
                  textTransform: 'uppercase',
                  fontFamily: 'ui-sans-serif, system-ui, -apple-system, sans-serif',
                  transition: 'color 0.4s ease',
                  display: 'block',
                  textAlign: 'center'
                }}
              >
                {series.name}
              </Link>
              
              {/* Optional: Floating preview image like some high-end sites, 
                  but Jadé's is often just the bold text. I'll stick to bold text for now. */}
            </li>
          ))}
        </ul>
      </div>
    </div>
  )
}
